import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';


const styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 200,
  },
  dense: {
    marginTop: 19,
  },
  menu: {
    width: 200,
  },
});

// const currencies = [
//   {
//     value: '210',
//     label: 'San Antonio (210)',
//   },
//   {
//     value: '512',
//     label: 'Austin (512)',
//   }
// ];

// const types = [
//   {
//     value: 'Call',
//     label: 'Phone Call'
//   },
//   {
//     value: 'Text',
//     label: 'Text'
//   }
// ];

const lists = [
  {
    value: 'id1',
    label: 'list1'
  },
  {
    value: 'id2',
    label: 'list2'
  }
];


class ScriptForm extends React.Component {
  state = {
    name: 'Cat in the Hat',
    age: '',
    description: '',
    campaignType: '',
    location1: '210',
    location2: '210',
    recipientList: '',
  };
handleChange = name => event => {
  this.setState({ [name]: event.target.value });
};
  render() {
    const { classes } = this.props;
    return (
      <React.Fragment>
        <Typography variant="h6" gutterBottom>
          Script
        </Typography>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={6}>
          <Button variant="contained" color="secondary" className={classes.button}>
        FIRST NAME
      </Button>
          </Grid>
          <Grid item xs={12} sm={6}>
          <Button variant="contained" color="secondary" className={classes.button}>
        LAST NAME
      </Button>
          </Grid>
          <Grid item xs={12} sm={6}>
          <Button variant="contained" color="secondary" className={classes.button}>
        SENDER FIRST NAME
      </Button>
          </Grid>
          <Grid item xs={12} sm={6}>
          <Button variant="contained" color="secondary" className={classes.button}>
        SENDER LAST NAME
      </Button>
          </Grid>
          <Grid item xs={12}>
          <TextField
            id="standard-multiline-flexible"
            multiline
            rowsMax="4"
            value={this.state.description}
            onChange={this.handleChange('description')}
            className={classes.textField}
            margin="normal"
          />
          </Grid>
          <Grid item xs={12} sm={6}>
          <TextField
            id="standard-select-list"
            select
            label="Who do you want to send to?"
            className={classes.textField}
            value={this.state.recipientList}
            onChange={this.handleChange('recipientList')}
            SelectProps={{
              MenuProps: {
                className: classes.menu,
              },
            }}
            helperText="Please select your recipient list"
            margin="normal"
          >
            {lists.map(option => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </TextField>
          </Grid>
          <Grid item xs={12}>
            <FormControlLabel
              control={<Checkbox color="secondary" name="saveAddress" value="yes" />}
              label="Use this address for payment details"
            />
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }
}

ScriptForm.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ScriptForm);